.App {
  text-align: center;
}

.App-logo {
  height: 12vmin;
  padding-right: 10px;
}
.App-logo.bob {
  animation: App-logo-wobble infinite alternate-reverse 1s cubic-bezier(0.73, 0.43, 0.41, 0.79);
}
.App-logo.flip {
  animation: App-logo-flip 1 running 1s cubic-bezier(0.47, 0, 0.8, 0.94);
  animation-fill-mode: forwards;
}

.App-bg {
  display: block;
  width: 100vw;
  position: absolute;
  z-index: 1;
  transform: scaleX(1.01) scaleY(1.01);
  filter: blur(5px);
}

.App-header {
  padding: 20px 20px 0 20px;
  height: 20vh;
  width: 100vw;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: rgb(255, 255, 255);
  box-sizing: border-box;
}
.App-form {
  min-height: 80vh;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-wobble {
  from { transform: translate(0, 0px) scaleY(1); }
  to { transform: translate(0, -5px) scaleY(1.05);}
}

@keyframes App-logo-flip {
  0% { transform: translate(0, 0) rotate(0deg); }
  60% { transform: translate(0, -50px) rotate(-216deg); }
  65% { transform: translate(0, -50px) rotate(-230deg); }
  90% { transform: translate(0, 10px) rotate(-360deg); }
  100% { transform: translate(0, 0) rotate(-360deg); }
}