body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Form-div {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  justify-content: space-between;
  color: black;
}

.Form-leftDiv .button {
  font-size: 0.7em;
}

.Form-leftDiv {
  flex: 1 1;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  flex-direction: column;
  padding: 20px;
  font-size: 2em;
}
.Form-leftDiv input[type=file] {
  display: none;
}

.Form-leftDiv .buttonDiv {
  display: flex;
  justify-content: space-between;
  flex-shrink: 0;
}
.Form-leftDiv .label {
  cursor: pointer;
  font-size: 1em;
  background: rgb(255, 215, 155);
  color: rgb(235, 77, 3);
  padding: 5px 20px;
  flex: 1 1;
  -webkit-transition: all .4s;
  transition: all .4s;
  border: 0;
  outline: 0;
  text-decoration: none;
  flex-shrink: 0;
}
.Form-leftDiv .label:hover {
  background: rgb(202, 54, 9);
  color: rgb(255, 255, 250);
  -webkit-transition: all .4s;
  transition: all .4s;
}
.Form-leftDiv .label:disabled {
  cursor: default;
  background: rgb(123, 139, 155);
  color: rgb(203, 209, 208);
  -webkit-transition: all .4s;
  transition: all .4s;
}
.Form-leftDiv .arrowDown {
  margin: 0;
  padding: 0;
  font-size: 1em;
  color: rgb(255, 223, 205);
  font-weight: bolder;
  -webkit-transform: scaleX(0.7);
          transform: scaleX(0.7);
  -webkit-transform-origin: center center;
          transform-origin: center center;
}

.Form-rightDiv {
  flex: 2 1;
  display: flex;
  flex-direction: column;
  padding: 20px 20px 20px 20px;
  font-size: 1.5em;
}
.Form-rightDiv h1 {
  text-align: left;
  padding: 0; margin: 0;
  color: rgb(155,155,155);
  font-weight: 100;
}
.Form-textResult {
  flex: 1 1;
  resize: none;
}

.Form-div svg {
  vertical-align: text-bottom;
}
.Synthesiser {
  display: inline-block;
  vertical-align: middle;
  min-width: 17em;
}

.Synthesiser * {
  vertical-align: middle;
}

.Synthesiser .box {
  display: inline-block;
  position: relative;
  vertical-align: top;
}

.Synthesiser .margin-right-10 {
  margin-right: 10px;
}

.Synthesiser .small-em {
  height: 0.7em;
}

.Synthesiser .orange-text {
  color: #eb4d03;
}
.App {
  text-align: center;
}

.App-logo {
  height: 12vmin;
  padding-right: 10px;
}
.App-logo.bob {
  animation: App-logo-wobble infinite alternate-reverse 1s cubic-bezier(0.73, 0.43, 0.41, 0.79);
}
.App-logo.flip {
  -webkit-animation: App-logo-flip 1 running 1s cubic-bezier(0.47, 0, 0.8, 0.94);
          animation: App-logo-flip 1 running 1s cubic-bezier(0.47, 0, 0.8, 0.94);
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.App-bg {
  display: block;
  width: 100vw;
  position: absolute;
  z-index: 1;
  -webkit-transform: scaleX(1.01) scaleY(1.01);
          transform: scaleX(1.01) scaleY(1.01);
  -webkit-filter: blur(5px);
          filter: blur(5px);
}

.App-header {
  padding: 20px 20px 0 20px;
  height: 20vh;
  width: 100vw;
  display: flex;
  position: relative;
  overflow: hidden;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 4vmin);
  color: rgb(255, 255, 255);
  box-sizing: border-box;
}
.App-form {
  min-height: 80vh;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

@keyframes App-logo-wobble {
  from { -webkit-transform: translate(0, 0px) scaleY(1); transform: translate(0, 0px) scaleY(1); }
  to { -webkit-transform: translate(0, -5px) scaleY(1.05); transform: translate(0, -5px) scaleY(1.05);}
}

@-webkit-keyframes App-logo-flip {
  0% { -webkit-transform: translate(0, 0) rotate(0deg); transform: translate(0, 0) rotate(0deg); }
  60% { -webkit-transform: translate(0, -50px) rotate(-216deg); transform: translate(0, -50px) rotate(-216deg); }
  65% { -webkit-transform: translate(0, -50px) rotate(-230deg); transform: translate(0, -50px) rotate(-230deg); }
  90% { -webkit-transform: translate(0, 10px) rotate(-360deg); transform: translate(0, 10px) rotate(-360deg); }
  100% { -webkit-transform: translate(0, 0) rotate(-360deg); transform: translate(0, 0) rotate(-360deg); }
}

@keyframes App-logo-flip {
  0% { -webkit-transform: translate(0, 0) rotate(0deg); transform: translate(0, 0) rotate(0deg); }
  60% { -webkit-transform: translate(0, -50px) rotate(-216deg); transform: translate(0, -50px) rotate(-216deg); }
  65% { -webkit-transform: translate(0, -50px) rotate(-230deg); transform: translate(0, -50px) rotate(-230deg); }
  90% { -webkit-transform: translate(0, 10px) rotate(-360deg); transform: translate(0, 10px) rotate(-360deg); }
  100% { -webkit-transform: translate(0, 0) rotate(-360deg); transform: translate(0, 0) rotate(-360deg); }
}
.SocialButton .btn,
.SocialButton .btn:before,
.SocialButton .btn .fa {
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  -webkit-transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
          transition-timing-function: cubic-bezier(0.31, -0.105, 0.43, 1.59);
}
.SocialButton {
  height: 60px;
  padding: 10px;
}
.SocialButton .btn:before {
  top: 90%;
  left: -110%;
}
.SocialButton .btn .fa {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}
.SocialButton .btn.github:before {
  background-color: #ff9800;
}
.SocialButton .btn.github .fa {
  color: #ff9800;
}
.SocialButton .btn:focus:before,
.SocialButton .btn:hover:before {
  top: -10%;
  left: -10%;
}
.SocialButton .btn:focus .fa,
.SocialButton .btn:hover .fa {
  color: #fff;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.SocialButton .btn {
  display: inline-block;
  background-color: #fff;
  width: 60px;
  height: 60px;
  line-height: 60px;
  text-align: center;
  position: relative;
  overflow: hidden;
  border-radius: 28%;
  box-shadow: 0 5px 15px -5px rgba(0,0,0,0.1);
  opacity: 0.99;
}
.SocialButton .btn:before {
  content: '';
  width: 120%;
  height: 120%;
  position: absolute;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.SocialButton .btn .fa {
  font-size: 38px;
  vertical-align: middle;
}
